@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  overflow-x: hidden;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}

a {
  text-decoration: none;
}

/* ----------------- */

textarea:focus,
input:focus,
input[type]:focus,
.uneditable-input:focus {
  outline: 0;
  box-shadow: none;
  border: 2px solid #ff9800;
  background-color: #ffffff;
}

.main {
  width: 100%;
  min-height: 100vh;
  /* display: flex; */
  /* background-color: #ebebeb; */
  background-color: #4b4b53;
}

/* ------- */
.layer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
  display: none;
}

/* ------------------------------- */

/* Works on Firefox */
.program-main-grid {
  scrollbar-width: 0.3rem;
  scrollbar-color: rgb(223, 223, 223) rgb(179, 179, 179);
}

/* Works on Chrome, Edge, and Safari */
.program-main-grid::-webkit-scrollbar {
  width: 0.3rem;
  height: 0.3rem;
}

.program-main-grid::-webkit-scrollbar-track {
  background: rgb(223, 223, 223);
  border-radius: 100vw;
}

.program-main-grid::-webkit-scrollbar-thumb {
  background-color: rgb(179, 179, 179);
  border-radius: 100vw;
}

/* ------------------------------- */

.command-list-container {
  width: 13%;
  height: 85vh;
  position: fixed;
  padding: 0rem 0rem 1rem 1rem;
}

.command-list {
  height: 100%;
  width: 100%;
  border-radius: 0.4rem;
  background-color: #fafafb;
  overflow-y: auto;
}

.command-list-wrapper {
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.command-list-width-box {
  width: 13%;
  height: 85vh;
}

.command-selector {
  width: 80%;
  display: flex;
  align-items: center;
  border-radius: 3px;
  margin: 1rem 0;
  padding: 0.5rem 0.3rem;
  background-image: linear-gradient(#63656a, #585b60, #63656a);
  color: #fef7f5;
  cursor: grab;
  font-weight: 600;
}

.command-selector:hover {
  transition: ease-in-out 0.15s;
  opacity: 0.4;
}

.command-selector i {
  color: white;
  margin-right: 0.5rem;
  font-size: 0.5rem;
}

/* ------------------------------- */
.center-section {
  display: flex;
  width: 100%;
  min-height: 85vh;
}

.main-interface-section {
  display: flex;
  width: 87%;
  min-height: 85vh;
}

/* ------------------------ */
.top-menu-container {
  min-height: 15vh;
  padding: 1rem 1rem;
}

.top-menu {
  height: calc(15vh - 2rem);
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  border-radius: 0.4rem;
}

.top-menu-button {
  color: rgb(38, 43, 71);
  border: solid 2px rgba(38, 43, 71, 0);
  font-weight: bold;
  border-radius: 0.2rem;
  text-align: center;
  padding: 0.2rem 0.8rem;
  font-size: 0.8rem;
  cursor: pointer;
  margin: 0 0 0 1rem;
  position: relative;
}

.top-menu-button i {
  margin-right: 0.2rem;
}

.top-menu-button:hover {
  border: solid 2px rgb(38, 43, 71);
  transition: ease 0.2s;
}

#top-menu-file-upload,
#top-menu-file-save {
  display: none;
}

