  justify-content: center;
  align-items: center;
  height: 1.5rem;
}

.dousa-box,
.dragged-box {
  border-radius: 0.2rem;
  font-weight: bold;
  /* color: rgba(0, 0, 0, 0.8); */
  color: #fef7f5;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 2.3rem;
  /* background-color: rgb(216, 221, 231); */
  /* background-color: rgb(192, 204, 226); */
  background-image: linear-gradient(#63656a, #585b60, #63656a);
  min-width: 7rem;
  cursor: pointer;
}

.dragged-box {
  opacity: 0.4;
}

.dousa-box p {
  cursor: pointer;
}

.no-value-circle {
  position: absolute;
  top: 2px;
  right: 2px;
  height: 0.4rem;
  width: 0.4rem;
  background-color: rgb(219, 219, 219);
  border-radius: 50%;
}

.typeDataInDousa {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  box-shadow: 0 2px 4px 1px rgba(255, 105, 135, 0.2);
  padding: 1rem;
  border-radius: 0.3rem;
  z-index: 20000 !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
