}

/* -- */
.fa-caret-down {
  color: rgba(0, 0, 0, 0.6);
}

.num-dropdown {
  margin-left: 0.2rem;
  position: relative;
  cursor: pointer;
}

.currentNum-container {
  display: flex;
  border: solid 0.1px rgba(0, 0, 0, 0.2);
