.typedata-input-line {
  margin: 0.3rem 0;
  display: flex;
  align-items: center;
  width: -webkit-max-content;
  width: max-content;
}

.typedata-input-line p {
  margin-right: 0.6rem;
}

.type-data-input {
  text-align: center;
  margin-right: 0.3rem;
}

.type-data-button {
  align-self: center;
}

.close-typedata {
  position: absolute;
  top: 3%;
  right: 3%;
}

.dousa-title:hover {
  /* color: rgba(0, 0, 0, 0.4); */
