  transition: ease-in-out 0.2s;
}

.dousa-group-kaishi i {
  margin: 0 0.2rem 0 0.6rem;
  font-size: 0.85rem;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.dousa-box i {
  margin: 0 0.2rem 0 0.6rem;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
}

.dousa-box i:hover {
  color: #f65e2d;
  transition: ease-in-out 0.1s;
