/* ================================ */
#settings-panel {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fafafb;
  width: 75%;
  height: 70%;
  border-radius: 0.7rem;
  z-index: 150000;
  padding: 2rem 1rem;
  display: none;
  justify-content: space-around;
  overflow-x: auto;
  overflow-y: auto;
}

/* ========= */
#close-settings-panel {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    opacity: 0.3;
    transition: opacity ease-in 0.3s;
    cursor: pointer;
}

#close-settings-panel:hover {
    opacity: 0.6;
}

/* ========================================= */

.settings-panel-block {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.settings-panel-block-label {
    font-size: 0.7rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

/* ====================================== */

.settings-panel-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: relative;
    margin-bottom: 0.9rem;
}

/* ============================== */

.settings-panel-item-label-container {
    display: flex;
    align-items: center;
}

/* ====== */
.settings-panel-item-label {
    font-size: 0.7rem;
}

/* ======= */
.settings-panel-item-info-container {
    margin: 0 0.3rem;
    cursor: pointer;
}

.settings-panel-item-info {
    opacity: 0.2;
}

.settings-panel-item-info-box {
    z-index: 100;
    display: none;
    width: -webkit-max-content;
    width: max-content;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    position: absolute;
    top: 115%;
    left: 40%;
    font-size: 0.7rem;
    font-weight: normal;
    padding: 0.2rem 0.3rem;
    color: white;
    background-color: #585b60;
    border-radius: 0.2rem;
}

.settings-panel-item-info-box p {
    height: -webkit-max-content;
    height: max-content;
}

/* ============================== */
.settings-panel-item-input {
    text-align: center;
    width: 40%;
}

/* ================================ */
#save-changes-button {
    position: absolute;
    left: 50%;
    bottom: 1rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
